.reviews__item {
  cursor: pointer;
  background: top / contain no-repeat;
  border: 1px solid #5b86a1;
  border-top: none;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.reviews__item:hover {
  border: none;
}

.reviews__item-bg {
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  background: center / cover no-repeat;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateY(90deg);
}

.reviews__item:hover .reviews__item-bg {
  opacity: 1;
  visibility: visible;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  transform: rotateY(0deg);
}

